import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Network from "../../Elements/Network/Network";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
import { IS_THEME } from "../../../../utils";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : isTagShop ? false : false
  const mediaIcon = !itemData.hotspot ? true : false
  const networkOnTop = (isTagEmbed && IS_THEME(19)) || !IS_THEME(59) && !isTagShop && !isTagbox ? true : false

  return <div className="tb_nc_media_wrap" role="button" tabindex="0" aria-label="Pop Up Button">

    {networkOnTop ?
      <div className="tb_nc_social_ tb_nc_social_top_">
        <Network networkClass={'tb_nc_social_ico'} network={itemData.network} isDefault={wall.ThemeRule.iconType === 1 ? true : false} ThemeRule={wall.ThemeRule} />
      </div> : null}

    {!autoPlayTagEmbed ? <MediaIcon itemData={itemData} IconClass={networkOnTop ? 'tb_nc_media_icon_te' : 'tb_nc_media_icon'} show={true} isCenter={isTagEmbed} /> : null}
    {autoPlay || autoPlayTagEmbed ?
      <Video VideoClass="tb_nc_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={true} muted={true} />
      :
      <Image ImageClass="tb_nc_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} hotspot={true} />
    }
  </div>
}
export default Media