import React, { Suspense, useRef, useEffect } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import { IS_THEME, PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { isTagEmbed, isTagShop, isTagbox } from '../../../../actions/api'
const trim_content_length = !isTagEmbed ? 200 : null;
const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall, counter, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const cardSize = { width: `${adjustWidth}%`, padding: personalization.padding / 2 };
  const mediaType = itemData.type !== 1 ? true : false;
  const rating = (itemData.rating > 0) ? true : false;
  const contentClass = (itemData.rating > 0) ? 'tb_nc_rating_content' : 'tb_nc_content'
  const textDecorate = (itemData.type === 1 && personalization.textDecorate) ? 'tb_nc_text_decoration tb_nc_text_post' : '';
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  const contentFont = itemData.font
  return (
    <div id={`tb-nc-post-${itemData.id}`} className={`tb_nc_post_wrapper ${isTagEmbed && IS_THEME(59) || isTagbox && IS_THEME(83) ? `tb_nc_post_gradient tb_nc_gradient-${counter}` : ''}`} style={cardSize} tb-network={itemData.network.icon} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} ref={postTrack} tabIndex="0" area-label={postTextContent} role="article">
      <div className="tb_nc_post_wrap_in">
        <div className="tb_nc_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
          <div className="tb_nc_post_media_wrapp" role="button" tabindex="0" aria-label="Pop Up Button">
            {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_nc_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : ''}
            {mediaType ? <Media key={`img_${itemData.id}`} itemData={itemData} wallID={wallID} ownerId={ownerId} wall={wall} /> : ''}
            {rating && !isTagEmbed && !IS_THEME(59) && !isTagShop ? <div className="tb_nc_rating__"><Rating rating={itemData.rating} network={itemData.network} ThemeID={personalization.widgetTheme == 59 ? personalization.widgetTheme : null} /></div> : ''}
          </div>
          <div className="tb_nc_contant_wrapper">
            <AuthorDetails mediaType={mediaType} ownerId={ownerId} itemData={itemData.id} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />

            {isTagEmbed ? '' : (ctaStatus ? <div className="tb_nc_post_cta"><CTAButton ctaClass={'tb_nc_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /></div> : '')}

            {rating && (isTagEmbed && (IS_THEME(59) || IS_THEME(19)) || isTagShop) ? <div className="tb_nc_rating__"><Rating rating={itemData.rating} network={itemData.network} ThemeID={personalization.widgetTheme == 59 ? personalization.widgetTheme : null} /></div> : ''}

            {(isTagShop && ThemeRule.hideContent) || ((!isTagShop && !ThemeRule.hideContent) || itemData.type == 1) ? <PostContent contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}

            {isTagEmbed ? (ctaStatus ? <div className="tb_nc_post_cta"><CTAButton ctaClass={'tb_nc_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /></div> : '') : ''}
          </div>
          {ThemeRule.socialAction ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={personalization.widgetTheme} actionClass={`tb_nc_social_action__`} /> : ''}
        </div>
      </div>
    </div>
  );
}
export default Card